@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.ticket-list-item {
  display: flex;
  justify-content: center;
  list-style: none;
  max-width: 566px;
  background-color: white;
  border-radius: 4px;
  margin-bottom: 20px;
  box-shadow: 0 1px 4px rgba(91, 137, 164, 0.25);
}

.ticket-list-item__left {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 1px solid #eceff1;
}
.ticket-list-item__right {
  margin-top: 15px;
  width: 100%;
}
.buyBtn {
  width: 160px;
  height: 56px;
  background-color: #ff6d00;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 4px;
  margin: 20px 20px 25px 20px;
}

.buyBtn:hover {
  background-color: #ff8124;
}

.buyBtn-text {
  display: block;
  margin: 0 auto;
  width: 50%;
  font-size: 1rem;
  line-height: 21px;
}

.ticket-list-item__content {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 25px;
}

.ticket-list-item__destination {
  text-align: right;
}

.ticket-list-item__origin,
.ticket-list-item__destination {
  display: flex;
  flex-direction: column;
}

.ticket-list-item__logo {
  margin-top: 25px;
}

.time {
  font-size: 2rem;
}
.name,
.date {
  font-size: 0.75rem;
}

.ticket-list-item__stops {
  line-height: 32px;
  text-transform: uppercase;
  font-size: 0.62rem;
  border-bottom: 1px solid #d2d5d6;
  padding-bottom: 1px;
}

.ticket-list-item__stops span {
  display: block;
  position: relative;
}
.ticket-list-item__stops span:after {
  content: "\2708";
  color: #d2d5d6;
  position: absolute;
  top: 53%;
}


* {
  margin: 0;
  padding: 0;
}
html {
  margin-left: calc(100vw - 100%);
  margin-right: 0;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: #f3f7fa;
  font-size: 16px;
}
#root {
}
.App {
  display: block;
  max-width: 780px;
  margin: 50px auto 0 auto;
}
.filters {
  margin-right: 20px;
  max-width: 240px;
  border-radius: 4px;
  font-size: 0.75rem;
  background-color: white;
  margin-bottom: 16px;
  display: inline-block;
  box-shadow: 0 1px 4px rgba(91, 137, 164, 0.25);
}
ul {
  display: inline-block;
  vertical-align: top;
}

.currency-filter {
  margin: 16px;
}

.filter {
  background-color: white;
  border-radius: 4px;
  display: inline-block;
}
.currency-filter span,
.filterBtn {
  text-transform: uppercase;
}

.currency-filter span {
  display: block;
  margin-bottom: 10px;
}

.btn {
  border: 1px solid #d2d5d6;
  color: #64b5f5;
}
.active {
  color: white;
  background-color: #2196f3;
  border: 1px solid #2196f3;
}
.active:hover {
  color: white;
}
.btn:not(.active):hover {
  background-color: #f2fcff;
  border: 1px solid #64b5f5;
  color: #64b5f5;
}
.btn:focus {
  box-shadow: none;
}

.btn-group {
  width: 100%;
}

.stops-filter label {
  width: 100%;
  display: inline-block;
  padding: 5px 0;
  margin: 0;
  position: relative;
}

.stops-filter label input {
  margin-left: 15%;
  opacity: 0;
}
.stops-filter span {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.stops-filter .amount {
  display: block;
  margin-left: 16px;
  margin-bottom: 10px;
}

.stops-filter label:hover {
  background-color: #f1fcff;
}

.stops-filter {
  padding-bottom: 16px;
}
.stops-filter .checkbox:before {
  content: "";
  position: absolute;
  top: -1px;
  left: -32px;
  height: 19px;
  width: 19px;
  border: 1px solid #d2d5d6;
  border-radius: 4px;
}
.stops-filter span {
  position: relative;
}
.stops-filter input:checked + .checkbox:after {
  content: "\2714";
  color: #2196f3;
  position: absolute;
  left: -28px;
}
.stops-filter input:checked + .checkbox:before {
  border: 1px solid #2196f3;
}

.stops-filter .only {
  opacity: 0;
}
.stops-filter label:hover .only {
  opacity: 1;
}

@keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.lds-spinner {
  position: relative;
}
.lds-spinner div {
  left: 94px;
  top: 48px;
  position: absolute;
  -webkit-animation: lds-spinner linear 1s infinite;
  animation: lds-spinner linear 1s infinite;
  background: #2196f3;
  width: 12px;
  height: 24px;
  border-radius: 40%;
  -webkit-transform-origin: 6px 52px;
  transform-origin: 6px 52px;
}
.lds-spinner div:nth-child(1) {
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-animation-delay: -0.916666666666667s;
  animation-delay: -0.916666666666667s;
}
.lds-spinner div:nth-child(2) {
  -webkit-transform: rotate(30deg);
  transform: rotate(30deg);
  -webkit-animation-delay: -0.833333333333333s;
  animation-delay: -0.833333333333333s;
}
.lds-spinner div:nth-child(3) {
  -webkit-transform: rotate(60deg);
  transform: rotate(60deg);
  -webkit-animation-delay: -0.75s;
  animation-delay: -0.75s;
}
.lds-spinner div:nth-child(4) {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  -webkit-animation-delay: -0.666666666666667s;
  animation-delay: -0.666666666666667s;
}
.lds-spinner div:nth-child(5) {
  -webkit-transform: rotate(120deg);
  transform: rotate(120deg);
  -webkit-animation-delay: -0.583333333333333s;
  animation-delay: -0.583333333333333s;
}
.lds-spinner div:nth-child(6) {
  -webkit-transform: rotate(150deg);
  transform: rotate(150deg);
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}
.lds-spinner div:nth-child(7) {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  -webkit-animation-delay: -0.416666666666667s;
  animation-delay: -0.416666666666667s;
}
.lds-spinner div:nth-child(8) {
  -webkit-transform: rotate(210deg);
  transform: rotate(210deg);
  -webkit-animation-delay: -0.333333333333333s;
  animation-delay: -0.333333333333333s;
}
.lds-spinner div:nth-child(9) {
  -webkit-transform: rotate(240deg);
  transform: rotate(240deg);
  -webkit-animation-delay: -0.25s;
  animation-delay: -0.25s;
}
.lds-spinner div:nth-child(10) {
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
  -webkit-animation-delay: -0.166666666666667s;
  animation-delay: -0.166666666666667s;
}
.lds-spinner div:nth-child(11) {
  -webkit-transform: rotate(300deg);
  transform: rotate(300deg);
  -webkit-animation-delay: -0.083333333333333s;
  animation-delay: -0.083333333333333s;
}
.lds-spinner div:nth-child(12) {
  -webkit-transform: rotate(330deg);
  transform: rotate(330deg);
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
.lds-spinner {
  width: 200px !important;
  height: 200px !important;
  -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
  transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
}

